<template>
  <div class="all">
    <div class="pubhead">发布</div>
    <div class="pubtitle">
      <input type="text" v-model="title" placeholder="请输入标题" />
    </div>
    <div class="cate">
      <div style="margin-left: 10px">请选择你要发布的地方</div>
      <div class="choose">
        <select v-model="item">
          <option v-for="(item,index) in cate" :key="index" :value="item">{{item.name}}</option>
        </select>
      </div>
    </div>
    <div class="pubcontent">
      <textarea
        style="word-break: break-all"
        v-model="content"
        placeholder="请输入内容"
      ></textarea>
    </div>
    <div class="tu">
      <!-- <van-uploader :after-read="afterRead" multiple  /> -->
      <van-uploader v-model="fileList" multiple max-count="1" />
    </div>
    <div class="btn">
      <van-button type="primary" @click="submit">确定</van-button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import { Button, Notify, Toast, Uploader } from "vant";
import { nanoid } from "nanoid";
export default {
  name:'public',
  data() {
    return {
      item:'',
      title: "",
      content: "",
      fileList: [],
      id: "",
      cate:[],
      a: 123,
    };
  },
  components: {
    [Button.name]: Button,
    [Notify.name]: Notify,
    [Uploader.name]: Uploader,
  },
  created(){
    // 获取文章分类
    axios({
      method:'get',
      url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_cate_list'
    }).then(res =>{
      // console.log(res.data.data);
      this.cate = res.data.data
    })
  },
  methods: {
    submit() {
      if(localStorage.getItem('uid') === null){
        Notify({
                type: "warning",
                message: '请先登录',
                duration: 1000,
              });
        this.$router.push('/login')
      }else{
      // console.log(localStorage.getItem('uid'));
      axios({
        method: "get",
        url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/upload/token",
      }).then((res) => {
        let fd = new FormData();
        fd.append("token", res.data.token);
        fd.append("file", this.fileList[0].file);
        fd.append("key", nanoid());
        axios({
          method: "post",
          url: "https://upload-z1.qiniup.com",
          data: fd,
        }).then((res) => {
          // console.log("http://toutiao.longxiaokj.com/" + res.data.key);
          // console.log(this.item);
          axios({
            method: "post",
            url: "https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/add_article",
            data: {
              title: this.title,
              content: this.content,
              cate_id: this.item._id,
              cate_name: this.item.name,
              author_id: localStorage.getItem("uid"),
              imageSrc: ["http://toutiao.longxiaokj.com/" + res.data.key],
            },
          }).then((res) => {
            if (!localStorage.getItem("token")) return Toast("请先登录");
            if (res.data.msg === "成功") {
              Notify({
                type: "primary",
                message: `发布成功`,
                duration: 1000,
              });
              this.$router.push("/index");
              this.title = '',
              this.content = '',
              this.item = '',
              this.fileList = []
            } else {
              Notify({
                type: "warning",
                message: res.data.msg,
                duration: 1000,
              });
            }
          });
        });
      });
     }
    },
  },
};
</script>

<style scoped>
.all {
  background-color: rgb(245, 247, 249);
  height: 620px;
  /* border: 1px solid red; */
}
.pubhead {
  /* border: 1px solid red; */
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: white;
  background-color: rgb(7, 193, 96);
}
.pubtitle {
  /* border: 1px solid red; */
  height: 40px;
  width: 99%;
  margin: 10px 0;
  overflow: hidden;
}
.pubtitle input {
  height: 100%;
  width: 99%;
  border: none;
  padding-left: 10px;
}
.cate {
  /* border: 1px solid red; */
  display: flex;
  justify-content: space-between;
  height: 50px;
  line-height: 50px;
  background-color: #fff;
  color: #666;
}
.choose {
  margin-right: 50px;
}
.choose select {
  /* border: 1px solid red; */
  height: 25px;
  width: 90px;
}
.pubcontent {
  /* border: 1px solid red; */
  height: 100px;
  margin: 10px 0;
}
.pubcontent textarea {
  width: 360px;
  border: none;
  overflow: visible;
  word-break: break-all;
  height: 50px;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 50px;
  padding: 10px 0px 50px 10px;
}
.tu {
  /* border: 1px solid red; */
  padding: 25px;
}
.tu svg {
  /* border:  1px solid red; */
  width: 30px;
  height: 30px;
}
.btn {
  /* border:  1px solid red; */
  text-align: center;
}
.btn button {
  width: 300px;
}
</style>